.hero{
    position: relative;
    width: 100%;
    height: 100vh;
}
.hero .containar{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
}

.hero-image {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100vh;
    object-fit: cover;
    object-position: center;
    z-index: -1;
}
.hero-text{
    width: 100%;
}
.hero h1{
    font-weight: 500;
    color: rgb(237, 237, 236);
    font-style: italic;
    text-align: left;
    font-size: 25.59pt;
    line-height: 22.75pt;
    font-family: CircusOfInnocence;
}
.hero .green-text-color{
    color: var(--green);
    font-size: 51.18pt;
    line-height: 55pt;
    font-style: italic;
}

.hero-social{
    width: 100%;
    margin-top: 121px;
    margin-bottom: 121px;
    display: flex;
}
.hero-social img{
    margin-right: 48px;
    width: 81px;
    height: 81px;
    border-radius: 50%;
    border: 4px solid var(--green);
}
.hero-social img:last-child{
    margin-right: 0;
}
  